<template>
  <v-app>
    <v-app-bar app hide-on-scroll>
      <v-app-bar-nav-icon
        class="hidden-md-and-up"
        @click.stop="drawer = !drawer"
      ></v-app-bar-nav-icon>
      <router-link to="/">
        <v-img contain src="./assets/logo-name1.png" max-width="100"></v-img>
      </router-link>
      <v-spacer></v-spacer>
      <div class="hidden-sm-and-down">
        <template v-for="(item, index) of menu">
          <v-btn
            :key="index + 'btn'"
            :to="item.link"
            :color="item.color"
            :icon="!item.title"
            v-on="item.click ? { click: item.click } : {}"
            class="elevation-0"
            rounded
          >
            <v-icon v-if="item.icon" class="mr-1">{{ item.icon }}</v-icon>
            <span v-if="item.title">{{ item.title }}</span>
          </v-btn>
          <span
            :key="index + 'span'"
            v-if="menu[menu.length - 1] !== item"
            class="mx-2"
            >|</span
          >
        </template>
      </div>
    </v-app-bar>
    <v-navigation-drawer
      v-model="drawer"
      app
      left
      :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-4'"
      disable-resize-watcher
    >
      <v-img
        contain
        max-width="150"
        class="mx-auto my-4"
        src="./assets/logo-name1.png"
      />
      <v-list>
        <v-list-item-group v-model="group" color="primary">
          <v-list-item class="text-button" :to="menu[0].link">
            <v-icon class="mr-2">{{ menu[0].icon }}</v-icon>
            {{ menu[0].title }}
          </v-list-item>
        </v-list-item-group>
      </v-list>
      <template v-slot:append>
        <v-card flat tile width="100%" color="primary" class="text-center">
          <v-card-text>
            <v-btn class="mx-3" icon>
              <v-icon size="24px">{{ menu[1].icon }}</v-icon>
            </v-btn>
            <v-btn class="mx-3" icon>
              <v-icon size="24px">{{ menu[2].icon }}</v-icon>
            </v-btn>
            <v-btn class="mx-3" icon>
              <v-icon size="24px">{{ menu[3].icon }}</v-icon>
            </v-btn>
            <v-btn
              class="mx-3"
              @click="$vuetify.theme.dark = !$vuetify.theme.dark"
              icon
            >
              <v-icon size="24px">{{ menu[4].icon }}</v-icon>
            </v-btn>
          </v-card-text>

          <v-divider></v-divider>

          <v-card-text class="white--text">
            @{{ new Date().getFullYear() }} —
            <strong>Videam</strong>
          </v-card-text>
        </v-card>
      </template>
    </v-navigation-drawer>
    <v-main>
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",

  components: {},

  data: function() {
    return {
      drawer: false,
      group: null,
      menu: [
        {
          icon: "mdi-home",
          title: "Home",
          color: "transparent",
          link: "/",
          click: null
        },
        {
          icon: "mdi-account",
          title: "Login",
          color: "primary",
          link: null,
          click: null
        },
        {
          icon: "mdi-account-plus",
          title: "Register",
          color: "primary",
          link: null,
          click: null
        },
        {
          icon: "mdi-translate",
          title: null,
          color: null,
          link: null,
          click: null
        },
        {
          icon: "mdi-lightbulb",
          title: null,
          color: null,
          link: null,
          click: this.switchTheme
        }
      ]
    };
  },

  watch: {
    group() {
      this.drawer = false;
    }
  },

  methods: {
    switchTheme() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    }
  }
};
</script>

<style>
#app {
  overflow-x: hidden;
}
</style>
