import Vue from "vue";
import VueRouter from "vue-router";
import i18n from "@/plugins/i18n";
import hashes from "@/locales/hashes";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home"
  },
  {
    path: "/watch",
    name: "Watch"
  },
  {
    path: "*",
    name: "404"
  }
];

const loadedRoutes = {};

for (const route of routes) {
  route.component = () =>
    import(
      /* webpackChunkName: "[request]" */ `../views/${route.name}.vue`
    ).then(({ default: component }) => {
      component.i18n = {};
      const name = component.name.toLowerCase();
      if (!loadedRoutes[name]) {
        loadedRoutes[name] = new Set();
      }
      if (!hashes[name]) return component;
      const locale = hashes[name][i18n.locale]
        ? i18n.locale
        : i18n.fallbackLocale;
      if (loadedRoutes[name].has(locale)) return component;
      component.beforeCreate.unshift(function() {
        if (!this.$i18n.messages[locale]) {
          import(
            /* webpackChunkName: "[request].translation" */ `@/locales/${name}/${locale}.json`
          ).then(({ default: messages }) => {
            loadedRoutes[name].add(locale);
            this.$i18n.mergeLocaleMessage(locale, messages);
          });
        }
      });
      return component;
    });
}

const router = new VueRouter({
  mode: "history",
  routes
});

if (process.env.NODE_ENV === "production") {
  router.onReady(() => {
    for (const route of routes) {
      const chunk = route.name.toLowerCase();
      if (!hashes[chunk]) continue;
      const locale = hashes[chunk][i18n.locale]
        ? i18n.locale
        : i18n.fallbackLocale;
      const link = document.createElement("link");
      link.rel =
        route.name === router.currentRoute.name ? "preload" : "prefetch";
      link.href = `/js/${chunk}.${locale}.translation.${hashes[chunk][locale]}.json`;
      document.head.appendChild(link);
    }
  });
}

export default router;
